.modal {
  display: none;
  z-index: 100;
  position: fixed;
  background-color: #ffffff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 320px;
  text-align: center;
  box-sizing: border-box;
  box-shadow: 0 7px 15px 0 rgba(0, 1, 1, 0.15);
  border-radius: 1px;
}
.modal__title {
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
}
.modal__text {
  font-size: 14px;
  line-height: 24px;
}
.modal__button {
  width: 100%;
  text-align: center;
  padding: 15px;
}
.modal-show {
  display: block;
  animation: move;
  animation-duration: 0.1s;
}
@keyframes move {
  from,
  to {
    bottom: 1%;
    left: 50%;
  }
  25%,
  75% {
    top: 100%;
  }
  50% {
    top: 30%;
  }
}
