  }
}
@media (min-width: 1200px) {
  .main-nav__item--current {
    position: relative;
  }
  .main-nav__item--current::before {
    position: absolute;
    content: "";
    bottom: -2px;
    right: 0;
    left: 0;
    height: 2px;
    background-color: #ffffff;
  }
}
.main-nav__link {
  position: relative;
  line-height: 32px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  display: block;
  padding-top: 16px;
  padding-bottom: 14px;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
  text-align: center;
  background-color: #283645;
}
.main-nav__link:hover,
.main-nav__link:focus {
  color: #d22856;
}
.main-nav__link:active {
  color: rgba(210, 40, 86, 0.3);
}
@media (min-width: 1200px) {
  .main-nav__link {
    font-size: 18px;
    line-height: 18px;
    font-weight: 400;
    text-transform: none;
    display: initial;
    padding: 0;
    background-color: initial;
  }
  .main-nav__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
